import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { SelectAjax } from 'components/common';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input, InputNumber, message, Spin } from 'antd';
import { getBuildingRoomById, createBuildingRoom, updateBuildingRoom } from 'services/buildingRoom';
import { getBuildingBases } from 'services/buildingBase';
import { getBuildings, getFloorByBuilding } from 'services/building';
import { getRoomTypes } from 'services/room';
// import { classroom } from 'dump/data';
import { getQueryParamId } from 'utils/urlUtil';

const { TextArea } = Input;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    groundworkTraining: '',
    building: '',
    floor: '',
    numberRoom: '',
    capacity: '',
    numberStudent: '',
    roomType: '',
    roomDevice: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formClassRoom' })
class FormClassRoom extends Component {
  state = {
    roomId: false,
    initialValue: {},
    loading: false,
  };

  componentDidMount = () => {
    if (getQueryParamId()) {
      this.setState({ roomId: getQueryParamId() });
      this.getClassRoomById(getQueryParamId());
    }
  };

  getClassRoomById = async id => {
    const res = await getBuildingRoomById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        const { roomId } = this.state;
        this.setState({ loading: true });
        try {
          if (!roomId) {
            await createBuildingRoom(values);
          } else {
            await updateBuildingRoom({ id: Number(roomId), ...values });
          }
          this.props.history.goBack();
        } catch (error) {
          message.error('C li xy ra');
        }
        this.setState({ loading: false });
      }
    });
  };

  render() {
    const { initialValue, loading } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { roomId } = this.state;
    const buildingBase = getFieldValue('buildingBase');
    const buildingId = getFieldValue('building');
    return (
      <Spin spinning={loading}>
        <Card title={roomId ? 'Cp nht danh mc phng hc' : 'Thm danh mc phng hc'}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              <Form.Item label="C s o to">
                {getFieldDecorator('buildingBase', {
                  initialValue: initialValue.buildingBaseId,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<SelectAjax service={getBuildingBases} />)}
              </Form.Item>
              <Form.Item label="Ta nh">
                {getFieldDecorator('building', {
                  initialValue: initialValue.buildingId,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<SelectAjax service={getBuildings} params={{ baseId: buildingBase }} />)}
              </Form.Item>
              <Form.Item label="Tng">
                {getFieldDecorator('floorId', {
                  initialValue: initialValue.building_FloorId,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<SelectAjax service={getFloorByBuilding} params={{ buildingId }} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="K hiu phng" hasFeedback>
                {getFieldDecorator('label', {
                  initialValue: initialValue.label,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<Input disabled={!!roomId} />)}
              </Form.Item>
              <Form.Item hasFeedback label="Sc cha" {...formItemLayout}>
                {getFieldDecorator('size', {
                  initialValue: initialValue.size,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<InputNumber />)}
              </Form.Item>
              <Form.Item label="Loi phng">
                {getFieldDecorator('roomTypeId', {
                  initialValue: initialValue.roomTypeId,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<SelectAjax service={getRoomTypes} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Thit b phng" hasFeedback>
                {getFieldDecorator('devices', {
                  initialValue: initialValue.devices,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lng nhp thng tin!',
                  //   },
                  // ],
                })(<TextArea rows={4} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={this.props.history.goBack} />
              </Form.Item>
            </Form>
          </Row>
        </Card>
      </Spin>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormClassRoom {...props} />
  </Page>
));



